/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useEffect } from '@wordpress/element';
import { useSelect, useDispatch } from '@wordpress/data';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { AlternativeId, ExperimentId } from '@nab/types';

/**
 * Internal dependencies
 */
import { StatusManager } from '../status-manager';

export type ResultsProviderProps = {
	readonly experimentId: ExperimentId;
	readonly alternativeId: AlternativeId;
	readonly firstDayOfWeek: number;
	readonly children: JSX.Element | JSX.Element[];
};

export const ResultsProvider = (
	props: ResultsProviderProps
): JSX.Element | null => {
	const { experimentId } = props;
	const experiment = useExperiment( experimentId );
	useInitEffect( props );

	if ( ! experiment ) {
		return null;
	} //end if

	const { children } = props;
	return (
		<div>
			<StatusManager />
			{ children }
		</div>
	);
};

// =====
// HOOKS
// =====

const useExperiment = ( id: ExperimentId ) =>
	useSelect( ( select ) => select( NAB_DATA ).getExperiment( id ) );

const useInitEffect = ( props: ResultsProviderProps ) => {
	const { setPageAttribute } = useDispatch( NAB_DATA );
	useSelect( ( select ) =>
		select( NAB_DATA ).getExperimentResults( props.experimentId )
	);
	useEffect( () => {
		const { experimentId, alternativeId, firstDayOfWeek } = props;

		setPageAttribute( 'editor/activeExperiment', experimentId );
		setPageAttribute( 'heatmap/activeAlternative', alternativeId );
		setPageAttribute( 'heatmap/activeFilter', 'timeToClick' );
		setPageAttribute( 'heatmap/activeMode', 'heatmap' );
		setPageAttribute( 'heatmap/activeResolution', 'desktop' );
		setPageAttribute( 'heatmap/dataStatus', { mode: 'waiting-script' } );
		setPageAttribute( 'heatmap/disabledFilterOptions', [] );
		setPageAttribute( 'heatmap/firstDayOfWeek', firstDayOfWeek % 7 );
		setPageAttribute( 'heatmap/intensity', 60 );
		setPageAttribute( 'heatmap/opacity', 70 );
	}, [] );
};
