/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useEffect } from '@wordpress/element';
import { useDispatch, useSelect, select as doSelect } from '@wordpress/data';
import { useInstanceId } from '@wordpress/compose';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { find } from 'lodash';
import { BigError, LoadingAnimation } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type {
	Dict,
	Heatmap,
	HeatmapErrorRationale,
	HeatmapFrameAction,
	Maybe,
} from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import {
	useActiveFilter,
	useActiveMode,
	useActiveResolution,
	useDisabledFilterOptions,
	useHeatmapIntensity,
	useHeatmapOpacity,
	useIFrameDimensions,
	useIsLocked,
	useResolutionChangeMode,
	useResolutions,
} from '../hooks';

export const HeatmapView = (): JSX.Element => {
	const instanceId = useInstanceId( HeatmapView );
	useResolutionChangeEffect( instanceId );
	useOpacityChangeEffect( instanceId );
	useRenderIFrameEffect( instanceId );

	const areResultsLoaded = useAreResultsLoaded();
	const awaitRendererScript = useRendererScriptWaiter();
	const contentUrl = useContentUrl();
	const [ resolutionChangeMode ] = useResolutionChangeMode();
	const { width, height } = useIFrameDimensions();
	const scale = useScale();

	const isIFrameBeingProcessed = 'none' !== resolutionChangeMode;

	return (
		<>
			<FeedbackMessage />
			<iframe
				id={ `nab-heatmap__iframe-${ instanceId }` }
				className="nab-heatmap__iframe"
				title={ _x(
					'Content of the heatmap',
					'text',
					'nelio-ab-testing'
				) }
				frameBorder="0"
				scrolling="no"
				style={ {
					display: areResultsLoaded ? 'block' : 'none',
					position: isIFrameBeingProcessed ? 'fixed' : undefined,
					left: isIFrameBeingProcessed ? '-99999px' : undefined,
					transform: `translate(-50%) scale(${ scale })`,
				} }
				width={ width }
				height={ height }
				src={ contentUrl }
				onLoad={ () => awaitRendererScript() }
			/>
		</>
	);
};

// ============
// HELPER VIEWS
// ============

const FeedbackMessage = () => {
	const isLocked = useIsLocked();
	const status = useDataStatus();

	if ( ! isLocked || ! status ) {
		return null;
	} //end if

	if ( 'error' === status.mode ) {
		return (
			<BigError
				className="nab-heatmap__results-loader"
				text={ getErrorMessage( status.rationale ) }
			/>
		);
	} //end if

	return (
		<LoadingAnimation
			className="nab-heatmap__results-loader"
			text={
				! [ 'processing', 'ready' ].includes( status.mode )
					? _x( 'Loading results…', 'text', 'nelio-ab-testing' )
					: _x( 'Processing results…', 'text', 'nelio-ab-testing' )
			}
		/>
	);
};

// =====
// HOOKS
// =====

const useDataStatus = () =>
	useSelect( ( select ) =>
		select( NAB_DATA ).getPageAttribute( 'heatmap/dataStatus' )
	);

const useAreResultsLoaded = () => 'ready' === useDataStatus()?.mode;

const useRendererScriptWaiter = () => {
	const { setPageAttribute } = useDispatch( NAB_DATA );
	return () =>
		void setTimeout( () => {
			const { getPageAttribute } = doSelect( NAB_DATA );
			const status = getPageAttribute( 'heatmap/dataStatus' );
			const isWaitingScript = 'waiting-script' === status?.mode;
			if ( isWaitingScript ) {
				setPageAttribute( 'heatmap/dataStatus', {
					mode: 'error',
					rationale: 'no-script',
				} );
			} //end if
		}, 30000 );
};

const useContentUrl = () =>
	useSelect(
		( select ): Maybe< string > => {
			const { getPageAttribute, getExperiment } = select( NAB_DATA );

			const activeExperimentId = getPageAttribute(
				'editor/activeExperiment'
			);
			const experiment = getExperiment( activeExperimentId ?? 0 );
			if ( ! experiment ) {
				return undefined;
			} //end if

			if ( isHeatmap( experiment ) ) {
				return experiment.links.heatmap;
			} //end if

			const activeAlternativeId = getPageAttribute(
				'heatmap/activeAlternative'
			);
			const alternative = find( experiment.alternatives, {
				id: activeAlternativeId,
			} );
			return alternative?.links.heatmap;
		}
	);

const useScale = () =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getPageAttribute(
				'heatmap/iframeContentScale'
			) ?? 1
	);

const useResolutionChangeEffect = ( instanceId: number | string ) => {
	const postMessage = usePostMessage( instanceId );
	const [ activeResolution ] = useActiveResolution();
	const [ resolutionChangeMode ] = useResolutionChangeMode();

	useEffect( () => {
		switch ( resolutionChangeMode ) {
			case 'reset-heights':
				postMessage( { type: 'reset-heights', activeResolution } );
				break;

			case 'fetch-lazy-load-images':
				postMessage( { type: 'fetch-lazy-load-images' } );
				break;

			case 'calculate-heights-in-double-sized-iframe':
				postMessage( {
					type: 'calculate-heights-in-double-sized-iframe',
					activeResolution,
				} );
				break;

			case 'calculate-actual-heights':
				postMessage( {
					type: 'calculate-actual-heights',
					activeResolution,
				} );
				break;

			case 'fix-heights':
				postMessage( { type: 'fix-heights', activeResolution } );
				break;

			case 'compute-click-coordinates':
				postMessage( {
					type: 'compute-click-coordinates',
					activeResolution,
				} );
				break;

			case 'recompute-click-coordinates':
				postMessage( {
					type: 'recompute-click-coordinates',
					activeResolution,
				} );
				break;

			case 'none':
				// Render results...
				break;
		} //end switch
	}, [ resolutionChangeMode ] );
};

const useOpacityChangeEffect = ( instanceId: number | string ) => {
	const postMessage = usePostMessage( instanceId );
	const [ opacity ] = useHeatmapOpacity();
	useEffect( () => {
		postMessage( { type: 'update-opacity', value: opacity } );
	}, [ opacity ] );
};

const useRenderIFrameEffect = ( instanceId: number | string ) => {
	const postMessage = usePostMessage( instanceId );
	const [ activeFilter ] = useActiveFilter();
	const [ activeMode ] = useActiveMode();
	const [ activeResolution ] = useActiveResolution();
	const [ disabledFilterOptions ] = useDisabledFilterOptions();
	const [ heatmapIntensity ] = useHeatmapIntensity();
	const [ opacity ] = useHeatmapOpacity();
	const [ resolutionChangeMode ] = useResolutionChangeMode();

	const areResultsLoaded = useAreResultsLoaded();
	const resolutions = useResolutions();

	useEffect( () => {
		if ( 'none' !== resolutionChangeMode ) {
			return;
		} //end if

		switch ( activeMode ) {
			case 'heatmap':
				postMessage( {
					type: 'render-results',
					mode: 'heatmap',
					areResultsLoaded,
					activeResolution,
					resolutions,
					heatmapIntensity,
					opacity,
				} );
				break;

			case 'scrollmap':
				postMessage( {
					type: 'render-results',
					mode: 'scrollmap',
					areResultsLoaded,
					activeResolution,
					resolutions,
					opacity,
				} );
				break;

			case 'confetti':
				postMessage( {
					type: 'render-results',
					mode: 'confetti',
					areResultsLoaded,
					activeFilter,
					disabledFilterOptions,
					opacity,
				} );
				break;
		} //end switch
	}, [
		activeFilter,
		activeMode,
		activeResolution,
		areResultsLoaded,
		JSON.stringify( disabledFilterOptions ),
		heatmapIntensity,
		resolutionChangeMode,
	] );
};

const usePostMessage = ( instanceId: number | string ) => (
	action: HeatmapFrameAction
) => {
	const id = `nab-heatmap__iframe-${ instanceId }`;
	const iframe = document.getElementById( id ) as HTMLIFrameElement;
	if ( iframe?.contentWindow?.postMessage ) {
		iframe.contentWindow.postMessage( action );
	} //end if
};

// =======
// HELPERS
// =======

function getErrorMessage( rationale: HeatmapErrorRationale ) {
	switch ( rationale ) {
		case 'no-script':
			return _x( 'Unable to render heatmap', 'text', 'nelio-ab-testing' );

		case 'experiment-not-found':
			return _x( 'Experiment not found', 'text', 'nelio-ab-testing' );

		case 'alternative-not-found':
			return _x( 'Page not found', 'text', 'nelio-ab-testing' );

		case 'promise-failed':
			return _x(
				'There was an error when retrieving data from the cloud',
				'text',
				'nelio-ab-testing'
			);

		case 'unknown':
			return _x( 'Something went wrong', 'text', 'nelio-ab-testing' );
	} //end switch
} //end getErrorMessage()

const isHeatmap = ( e: Dict ): e is Heatmap => 'nab/heatmap' === e.type;
