/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import {
	BaseControl,
	ButtonGroup,
	Button,
	Dashicon,
	SelectControl,
} from '@wordpress/components';
import { useInstanceId } from '@wordpress/compose';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { sortBy } from 'lodash';
import type { ConfettiType, HeatmapMode } from '@nab/types';

/**
 * Internal dependencies
 */
import { FilterOptionsList } from '../filter-options-list';
import { useActiveFilter, useActiveResolution, useIsLocked } from '../hooks';

export type FilterSectionProps = {
	readonly kind: HeatmapMode;
};

export const FilterSection = ( { kind }: FilterSectionProps ): JSX.Element =>
	'confetti' === kind ? <ConfettiFilterSection /> : <RegularFilterSection />;

// =======
// HELPERS
// =======

const ConfettiFilterSection = (): JSX.Element => {
	const isLocked = useIsLocked();
	const [ activeFilter, setActiveFilter ] = useActiveFilter();
	const options = useConfettiOptions();

	return (
		<>
			<SelectControl
				disabled={ isLocked }
				label={ _x( 'Filter', 'text', 'nelio-ab-testing' ) }
				value={ activeFilter } // e.g: value = [ 'a', 'c' ]
				onChange={ ( value ) => setActiveFilter( value ) }
				options={ options }
			/>
			<FilterOptionsList />
		</>
	);
};

const RegularFilterSection = () => {
	const instanceId = useInstanceId( FilterSection );
	const isLocked = useIsLocked();
	const [ activeResolution, setActiveResolution ] = useActiveResolution();

	return (
		<BaseControl
			id={ `nab-resolution-selector__${ instanceId }` }
			label={ _x( 'Resolution', 'text', 'nelio-ab-testing' ) }
		>
			<ButtonGroup
				id={ `nab-resolution-selector__${ instanceId }` }
				className="nab-resolution-selector"
			>
				<Button
					isSecondary={ 'desktop' !== activeResolution }
					isPrimary={ 'desktop' === activeResolution }
					disabled={ isLocked }
					className={ classnames( {
						'nab-resolution-selector__option': true,
					} ) }
					title={ _x( 'Desktop', 'text', 'nelio-ab-testing' ) }
					onClick={ () => setActiveResolution( 'desktop' ) }
				>
					<Dashicon icon="desktop" />
				</Button>
				<Button
					isSecondary={ 'tablet' !== activeResolution }
					isPrimary={ 'tablet' === activeResolution }
					disabled={ isLocked }
					className={ classnames( {
						'nab-resolution-selector__option': true,
					} ) }
					title={ _x( 'Tablet', 'text', 'nelio-ab-testing' ) }
					onClick={ () => setActiveResolution( 'tablet' ) }
				>
					<Dashicon icon="tablet" />
				</Button>
				<Button
					isSecondary={ 'smartphone' !== activeResolution }
					isPrimary={ 'smartphone' === activeResolution }
					disabled={ isLocked }
					className={ classnames( {
						'nab-resolution-selector__option': true,
					} ) }
					title={ _x( 'Smartphone', 'text', 'nelio-ab-testing' ) }
					onClick={ () => setActiveResolution( 'smartphone' ) }
				>
					<Dashicon icon="smartphone" />
				</Button>
			</ButtonGroup>
		</BaseControl>
	);
};

// =====
// HOOKS
// =====

const useConfettiOptions = () => CONFETTI_OPTIONS;

// ====
// DATA
// ====

const CONFETTI_OPTIONS: ReadonlyArray< {
	readonly value: ConfettiType;
	readonly label: string;
} > = sortBy(
	[
		{
			value: 'country',
			label: _x( 'Country', 'text', 'nelio-ab-testing' ),
		},
		{
			value: 'device',
			label: _x( 'Device Type', 'text', 'nelio-ab-testing' ),
		},
		{
			value: 'os',
			label: _x( 'Operating System', 'text', 'nelio-ab-testing' ),
		},
		{
			value: 'browser',
			label: _x( 'Browser', 'text', 'nelio-ab-testing' ),
		},
		{
			value: 'dayOfWeek',
			label: _x( 'Day of Week', 'text', 'nelio-ab-testing' ),
		},
		{
			value: 'hourOfDay',
			label: _x( 'Time of Day', 'text', 'nelio-ab-testing' ),
		},
		{
			value: 'timeToClick',
			label: _x( 'Time to Click', 'text', 'nelio-ab-testing' ),
		},
		{
			value: 'windowWidth',
			label: _x( 'Window Width', 'text', 'nelio-ab-testing' ),
		},
	],
	'label'
);
