/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';
import { useIFrameDimensions } from '../hooks';
import { Header } from '../header';
import { HeatmapView } from '../heatmap-view';
import { Sidebar } from '../sidebar';

export const Layout = (): JSX.Element => {
	const height = useHeight();
	return (
		<div className="nab-heatmap-results-layout">
			<Header />
			<div className="nab-heatmap-results-layout__body">
				<Sidebar />

				<div
					className="nab-heatmap-results-layout__content"
					role="region"
					/* translators: accessibility text for the content landmark region. */
					aria-label={ _x(
						'Results content',
						'text',
						'nelio-ab-testing'
					) }
					tabIndex={ -1 }
					style={ {
						height,
					} }
				>
					<HeatmapView />
				</div>
			</div>
		</div>
	);
};

// =====
// HOOKS
// =====

const useHeight = () => {
	const { height } = useIFrameDimensions();
	const scale = useSelect(
		( select ) =>
			select( NAB_DATA ).getPageAttribute(
				'heatmap/iframeContentScale'
			) ?? 1
	);
	return ( height + 200 ) * scale;
};
