/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Button, Dashicon } from '@wordpress/components';
import { _x } from '@wordpress/i18n';
import { useSelect, useDispatch } from '@wordpress/data';

/**
 * External dependencies
 */
import { SubscribeWithCouponButton } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import { STORE_NAME as NAB_EXPERIMENTS } from '@nab/experiments';
import type { Experiment } from '@nab/types';

/**
 * Internal dependencies
 */
import { getLetter } from '../../../results/components/utils';
import './style.scss';

export const Header = (): JSX.Element | null => {
	const Icon = useIcon();
	const {
		name,
		type,
		status,
		alternative,
		isBeingStopped,
	} = useExperimentData();
	const { stopExperiment } = useDispatch( NAB_DATA );

	if ( ! Icon ) {
		return null;
	} //end if

	return (
		<div className="nab-heatmap-results-header">
			<div className="nab-heatmap-results-header__info">
				<Icon className="nab-heatmap-results-header__experiment-icon" />
				<h1 className="nab-heatmap-results-header__title">{ name }</h1>
				<SubscribeWithCouponButton
					className={
						'finished' === status
							? 'nab-heatmap-results-header__right-promo-button'
							: undefined
					}
				/>
			</div>

			{ type === 'nab/heatmap' && status === 'running' && (
				<div className="nab-heatmap-results-header__settings">
					{ isBeingStopped ? (
						<span className="nab-heatmap-results-header__stop-button nab-heatmap-results-header__stop-button--is-saving">
							<Dashicon icon="cloud" />
							{ _x( 'Stopping…', 'text', 'nelio-ab-testing' ) }
						</span>
					) : (
						<Button
							isPrimary
							className="nab-heatmap-results-header__stop-button"
							onClick={ stopExperiment }
						>
							{ _x( 'Stop', 'command', 'nelio-ab-testing' ) }
						</Button>
					) }
				</div>
			) }

			{ type !== 'nab/heatmap' && (
				<div className="nab-heatmap-results-header__variant">
					<span
						className={ `nab-alternative__letter nab-alternative__letter--${ alternative }` }
					>
						{ getLetter( alternative ) }
					</span>
				</div>
			) }
		</div>
	);
};

// =====
// HOOKS
// =====

const useIcon = () => {
	const { type } = useExperimentData();
	return useSelect( ( select ) => {
		const { getExperimentTypes } = select( NAB_EXPERIMENTS );
		const experimentType = getExperimentTypes()[ type ];
		return experimentType?.icon ?? ( () => <></> );
	} );
};

const useExperimentData = (): typeof NO_DATA =>
	useSelect( ( select ) => {
		const { getPageAttribute, getExperiment } = select( NAB_DATA );

		const activeExperiment = getPageAttribute( 'editor/activeExperiment' );
		const experiment = getExperiment( activeExperiment ?? 0 );
		if ( ! experiment ) {
			return NO_DATA;
		} //end if

		const { type, name, status, alternatives } = experiment;
		const activeAlternativeId = getPageAttribute(
			'heatmap/activeAlternative'
		);
		const alternativeIndex = alternatives.findIndex(
			( a ) => a.id === activeAlternativeId
		);

		return {
			name,
			type,
			status,
			alternative: Math.max( 0, alternativeIndex ),
			isBeingStopped: !! getPageAttribute(
				'editor/isExperimentBeingStopped'
			),
		};
	} );

// =======
// HELPERS
// =======

const NO_DATA = {
	name: '',
	type: '',
	status: 'running' as Experiment[ 'status' ],
	alternative: 0,
	isBeingStopped: false,
};
