/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { RangeControl } from '@wordpress/components';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { usePageAttribute } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';
import { useIsLocked } from '../hooks';

export const HeatmapIntensity = (): JSX.Element => {
	const isLocked = useIsLocked();
	const [ heatmapIntensity, setHeatmapIntensity ] = usePageAttribute(
		'heatmap/intensity',
		60
	);

	return (
		<RangeControl
			disabled={ isLocked }
			className="nab-heatmap-intensity"
			label={ _x( 'Heat Intensity', 'text', 'nelio-ab-testing' ) }
			value={ heatmapIntensity }
			onChange={ ( value ) => setHeatmapIntensity( value || 40 ) }
			min={ 40 }
			max={ 100 }
		/>
	);
};
