/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useSelect } from '@wordpress/data';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';
import { Summary } from './summary';
import { Description } from '../../../results/components/sidebar/description';
import { KindSelector } from '../kind-selector';
import { Advanced } from './advanced';

export const Sidebar = (): JSX.Element => {
	const style = useSidebarStyle();
	return (
		<div className="nab-heatmap-results-sidebar" style={ style }>
			<div className="nab-heatmap-results-sidebar__element-wrapper">
				<KindSelector />
				<Summary />
				<Description />
				<Advanced />
			</div>
		</div>
	);
};

// =====
// HOOKS
// =====

const useSidebarStyle = () =>
	useSelect( ( select ) => {
		const { getPageAttribute } = select( NAB_DATA );
		const { applyFix, ...dims } = getPageAttribute( 'sidebarDimensions' );
		return applyFix ? dims : undefined;
	} );
