/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { sprintf, _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { trim } from 'lodash';
import { numberFormat, compactInteger } from '@nab/i18n';
import { isEmpty } from '@nab/utils';

export function getAlternativeName( index: number, name = '' ): string {
	name = trim( name );
	if ( ! isEmpty( name ) ) {
		return name;
	} //end if

	if ( 0 === index ) {
		return _x( 'Control Version', 'text', 'nelio-ab-testing' );
	} //end if

	return sprintf(
		/* translators: a letter, such as A, B, or C */
		_x( 'Variant %s', 'text', 'nelio-ab-testing' ),
		getLetter( index )
	);
} //end getAlternativeName()

export function getLetter( index: number ): string {
	const letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
	if ( index >= letters.length ) {
		return `${ index - letters.length + 1 }`;
	} //end if

	return letters.charAt( index );
} //end getLetter()

export function getNumberLabel( value?: number ): JSX.Element {
	if ( 0 === value ) {
		return <>0</>;
	} //end if

	if ( ! value ) {
		return <>–</>;
	} //end if

	return <>{ numberFormat( value ) }</>;
} //end getNumberLabel()

export function getMoneyLabel( value?: number, currency = 'USD' ): string {
	if ( 0 === value ) {
		return `${ currency }0`;
	} //end if

	if ( ! value ) {
		return '–';
	} //end if

	return `${ currency }${ compactInteger( value ) }`;
} //end getMoneyLabel()

export function getPercentageLabel( num: number ): JSX.Element {
	if ( ! num ) {
		return <>–</>;
	} //end if

	return <>{ numberFormat( num ) }%</>;
} //end getPercentageLabel()

export function getImprovementLabel( num: number ): JSX.Element {
	if ( ! num ) {
		return <></>;
	} //end if

	return (
		<>
			{ num < 0 ? '↓' : '↑' }
			{ numberFormat( Math.abs( num ) ) }%
		</>
	);
} //end getImprovementLabel()
