/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { PanelRow } from '@wordpress/components';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { numberFormat } from '@nab/i18n';

export type PageViewsProps = {
	readonly views?: number;
};

export const PageViews = ( { views }: PageViewsProps ): JSX.Element => (
	<PanelRow className="nab-experiment-summary__pageviews">
		<span>{ _x( 'Page Views', 'text', 'nelio-ab-testing' ) }</span>
		<span>
			{ 'undefined' !== typeof views ? numberFormat( views ) : '-' }
		</span>
	</PanelRow>
);
