/**
 * External dependencies
 */
import { usePageAttribute } from '@nab/data';
import type {
	Dims,
	HeatmapResolutionChangeMode,
	ConfettiType,
	HeatmapMode,
	HeatmapResolution,
} from '@nab/types';

export const useActiveFilter = (): [
	ConfettiType,
	( val: ConfettiType ) => void
] => {
	const setDisFilterOpts = useDisabledFilterOptions()[ 1 ];
	const [ activeFilter, doSetActiveFilter ] = usePageAttribute(
		'heatmap/activeFilter',
		'timeToClick'
	);

	const setActiveFilter = ( filter: ConfettiType ): void => {
		if ( filter === activeFilter ) {
			return;
		} //end if
		setDisFilterOpts( [] );
		doSetActiveFilter( filter );
	};

	return [ activeFilter, setActiveFilter ];
};

export const useDisabledFilterOptions = (): [
	ReadonlyArray< string >,
	( val: ReadonlyArray< string > ) => void
] => usePageAttribute( 'heatmap/disabledFilterOptions', [] );

export const useActiveMode = (): [
	HeatmapMode,
	( val: HeatmapMode ) => void
] => {
	const [ activeResolution, setActiveResolution ] = useActiveResolution();
	const [ activeMode, doSetActiveMode ] = usePageAttribute(
		'heatmap/activeMode',
		'heatmap'
	);

	const setActiveMode = ( mode: HeatmapMode ) => {
		if ( mode === activeMode ) {
			return;
		} //end if
		doSetActiveMode( mode );
		if ( 'confetti' === mode && 'desktop' !== activeResolution ) {
			setActiveResolution( 'desktop' );
		} //end if
	};

	return [ activeMode, setActiveMode ];
};

export const useActiveResolution = (): [
	HeatmapResolution,
	( val: HeatmapResolution ) => void
] => {
	const [ activeResolution, doSetActiveResolution ] = usePageAttribute(
		'heatmap/activeResolution',
		'desktop'
	);
	const setIFrameHeight = useIFrameContentHeight()[ 1 ];
	const setResolutionChangeMode = useResolutionChangeMode()[ 1 ];

	const setActiveResolution = ( resolution: HeatmapResolution ) => {
		if ( resolution === activeResolution ) {
			return;
		} //end if
		doSetActiveResolution( resolution );
		setIFrameHeight( 0 );
		setResolutionChangeMode( 'reset-heights' );
	};

	return [ activeResolution, setActiveResolution ];
};

export const useIFrameContentHeight = (): [ number, ( val: number ) => void ] =>
	usePageAttribute( 'heatmap/iframeContentHeight', 0 );

export const useResolutionChangeMode = (): [
	HeatmapResolutionChangeMode,
	( val: HeatmapResolutionChangeMode ) => void
] => usePageAttribute( 'heatmap/resolutionChangeMode', 'none' );

export const useHeatmapIntensity = (): [ number, ( val: number ) => void ] =>
	usePageAttribute( 'heatmap/intensity', 70 );

export const useHeatmapOpacity = (): [ number, ( val: number ) => void ] =>
	usePageAttribute( 'heatmap/opacity', 70 );

export const useIFrameDimensions = (): Dims => {
	const [ activeResolution ] = useActiveResolution();
	const [ height ] = useIFrameContentHeight();
	const resolutions = useResolutions();
	return {
		height: height || resolutions[ activeResolution ].height,
		width: resolutions[ activeResolution ].width,
	};
};

export const useIsLocked = (): boolean =>
	usePageAttribute( 'isLocked', false )[ 0 ];

export const useResolutions = (): Record< HeatmapResolution, Dims > => ( {
	desktop: {
		width: 1600,
		height: 900,
	},
	tablet: {
		width: 1024,
		height: 576,
	},
	smartphone: {
		width: 460,
		height: 818,
	},
} );
