/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { RangeControl } from '@wordpress/components';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { usePageAttribute } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';
import { useIsLocked } from '../hooks';

export const Opacity = (): JSX.Element => {
	const isLocked = useIsLocked();
	const [ opacity, setOpacity ] = usePageAttribute( 'heatmap/opacity', 70 );

	return (
		<RangeControl
			disabled={ isLocked }
			className="nab-heatmap-opacity"
			label={ _x( 'Opacity', 'text', 'nelio-ab-testing' ) }
			value={ opacity }
			onChange={ ( value ) => setOpacity( value ?? 25 ) }
			min={ 25 }
			max={ 75 }
		/>
	);
};
