/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { dispatch } from '@wordpress/data';
import domReady from '@wordpress/dom-ready';
import { StrictMode, render } from '@wordpress/element';

/**
 * External dependencies
 */
import { debounce } from 'lodash';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import { registerCoreExperiments } from '@nab/experiment-library';
import {
	isExperimentTooOldToHaveHeatmaps,
	createStagingNotice,
} from '@nab/utils';
import type { AlternativeId, ExperimentId, SiteId } from '@nab/types';

/**
 * Internal dependencies
 */
import { ResultsNotAvailable } from './components/results-not-available';
import { ResultsProvider } from './components/provider';
import { Layout } from './components/layout';

type Settings = {
	readonly alternativeId: AlternativeId;
	readonly awsAuthToken: string;
	readonly endDate: string | false;
	readonly experimentId: ExperimentId;
	readonly firstDayOfWeek: number;
	readonly isStaging: boolean;
	readonly siteId: SiteId;
	readonly timezone: string;
};

export function initPage( id: string, settings: Settings ): void {
	const content = document.getElementById( id );

	if ( isExperimentTooOldToHaveHeatmaps( settings.endDate || '' ) ) {
		return render(
			<StrictMode>
				<ResultsNotAvailable />
			</StrictMode>,
			content
		);
	} //end if

	const { experimentId, alternativeId, isStaging, firstDayOfWeek } = settings;

	if ( isStaging ) {
		createStagingNotice();
	} //end if

	setPageAttributesForAwsRequest( settings );
	registerCoreExperiments();

	render(
		<StrictMode>
			<ResultsProvider
				experimentId={ experimentId }
				alternativeId={ alternativeId }
				firstDayOfWeek={ firstDayOfWeek }
			>
				<Layout />
			</ResultsProvider>
		</StrictMode>,
		content
	);
} //end initPage()

function setPageAttributesForAwsRequest( settings: Settings ) {
	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/awsAuthToken',
		settings.awsAuthToken
	);
	dispatch( NAB_DATA ).setPageAttribute( 'heatmap/siteId', settings.siteId );
	dispatch( NAB_DATA ).setPageAttribute(
		'heatmap/timezone',
		settings.timezone
	);
} //end setPageAttributesForAwsRequest()

function fixDimensionsOnResize() {
	const width = document.body.clientWidth;
	const { setPageAttribute } = dispatch( NAB_DATA );

	let scale;
	if ( width < 480 ) {
		scale = 0.2;
	} else if ( width < 600 ) {
		scale = 0.3;
	} else if ( width < 782 ) {
		scale = 0.4;
	} else if ( width < 1080 ) {
		scale = 0.3;
	} else if ( width < 1280 ) {
		scale = 0.4;
	} else if ( width < 1440 ) {
		scale = 0.45;
	} else if ( width < 1600 ) {
		scale = 0.6;
	} else {
		scale = 0.7;
	} //end if

	setPageAttribute( 'heatmap/iframeContentScale', scale );

	const height = window.innerHeight;
	const wpAdminBar = document.getElementById( 'wpadminbar' );
	const adminBarHeight = wpAdminBar
		? wpAdminBar.getBoundingClientRect().height
		: 0;

	setPageAttribute( 'sidebarDimensions', {
		top: adminBarHeight,
		height: `${ height - adminBarHeight }px`,
		applyFix: 782 <= window.innerWidth, // medium breakpoint
	} );
} //end fixDimensionsOnResize()
window.addEventListener( 'resize', debounce( fixDimensionsOnResize, 100 ) );
domReady( fixDimensionsOnResize );
