/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { PanelBody, Button } from '@wordpress/components';
import { useSelect, useDispatch } from '@wordpress/data';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';
import { useIsLocked } from '../../hooks';

export const Advanced = (): JSX.Element | null => {
	const isLocked = useIsLocked();
	const isVisible = useIsVisible();
	const repositionData = useRepositionData();

	if ( ! isVisible ) {
		return null;
	} //end if

	return (
		<PanelBody
			initialOpen={ false }
			className="nab-advanced"
			title={ _x( 'Advanced', 'text', 'nelio-ab-testing' ) }
		>
			<p>
				{ _x(
					'Under some circumstances, when your page loads content dynamically, the results may be shifted from their correct position. Try re-positioning your tracking data to re-render the results properly:',
					'user',
					'nelio-ab-testing'
				) }
			</p>

			<Button
				className="nab-advanced__reposition-button"
				disabled={ isLocked }
				isSecondary
				onClick={ repositionData }
			>
				{ _x( 'Re-position data', 'command', 'nelio-ab-testing' ) }
			</Button>
		</PanelBody>
	);
};

// =====
// HOOKS
// =====

const useIsVisible = () =>
	useSelect(
		( select ) =>
			'scrollmap' !==
			select( NAB_DATA ).getPageAttribute( 'heatmap/activeMode' )
	);

const useRepositionData = () => {
	const { setPageAttribute, lock } = useDispatch( NAB_DATA );
	return () => {
		lock();
		setPageAttribute(
			'heatmap/resolutionChangeMode',
			'recompute-click-coordinates'
		);
	};
};
