/**
 * WordPress dependencies
 */
import { useEffect } from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';

/**
 * Internal dependencies
 */
import { useResolutionChangeMode } from '../hooks';

export const StatusManager = (): null => {
	const status = useStatus();
	const [ resChangeMode ] = useResolutionChangeMode();
	const lock = useLock();

	useEffect( () => {
		lock( 'ready' !== status || 'none' !== resChangeMode );
	}, [ status, resChangeMode ] );

	return null;
};

// =====
// HOOKS
// =====

const useStatus = () =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getPageAttribute( 'heatmap/dataStatus' )?.mode ??
			''
	);

const useLock = () => {
	const { lock, unlock } = useDispatch( NAB_DATA );
	return ( v ) => ( v ? lock() : unlock() );
};
